import { GroupControls } from '../typed-forms/group-controls';
import { GrFormGroup } from '../typed-forms/gr-form-group';
import { ControlBinder } from '../control-binder';
import { GrAbstract, Options } from './abstract';

export class GrGroup extends GrAbstract {
  public constructor(public readonly group: {[key: string]: any}, options?: Options) {
    super(options);
  }

  public buildGroup<T extends GroupControls>(controls: T): GrFormGroup<T> {
    const group = new GrFormGroup<T>(controls, this.validators, this.asyncValidators);
    ControlBinder.setUpSubscriptions(
      {
        disabled: this.disabled,
        valueOverride: this.valueOverride
      },
      group
    );
    return group;
  }
}
