import {AbstractControl} from '@angular/forms';
import {GrFormArray} from '../typed-forms/gr-form-array';
import {ControlBinder} from '../control-binder';
import {GrAbstract, Options} from './abstract';

export class GrArray extends GrAbstract {
  public constructor(
    public readonly array: any[], options?: Options) {
    super(options);
  }

  public buildArray<T extends AbstractControl>(controls: T[]): GrFormArray<T> {
    const array = new GrFormArray<T>(controls, this.validators, this.asyncValidators);
    ControlBinder.setUpSubscriptions(
      {
        disabled: this.disabled,
        valueOverride: this.valueOverride
      },
      array
    );
    return array;
  }
}
