import {AbstractControlOptions, AsyncValidatorFn, ValidatorFn} from '@angular/forms';
import {Observable} from 'rxjs';

export type FormValidators = ValidatorFn | ValidatorFn[] | AbstractControlOptions | null;
export type AsyncFormValidators = AsyncValidatorFn | AsyncValidatorFn[] | null;

export interface FormDisableOptions {
  enabled: boolean;
  onlySelf?: boolean;
  emitEvent?: boolean;
}
export type FormDisable = boolean | FormDisableOptions;


export interface ValueOverride {
  value: any;
  options?: {
    onlySelf?: boolean;
    emitEvent?: boolean;
    emitModelToViewChange?: boolean;
    emitViewToModelChange?: boolean;
  };
}

export interface Options {
  validators?: FormValidators;
  asyncValidators?: AsyncFormValidators;
  disabler?: Observable<FormDisable>;
  valueOverrider?: Observable<ValueOverride>;
}

export abstract class GrAbstract {
  protected readonly validators: FormValidators;
  protected readonly asyncValidators: AsyncFormValidators;
  protected readonly disabled: Observable<FormDisable>;
  protected readonly valueOverride: Observable<ValueOverride>;

  public constructor(options: Options) {
    this.validators = options ? options.validators : [];
    this.asyncValidators = options ? options.asyncValidators : [];
    this.disabled = options ? options.disabler : null;
    this.valueOverride = options ? options.valueOverrider : null;
  }
}
