import { Observable } from 'rxjs';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import {FormDisable, FormDisableOptions, ValueOverride} from './build-models/abstract';

export abstract class ControlBinder {

  public static setUpSubscriptions(
    options: { disabled?: Observable<FormDisable>, valueOverride?: Observable<ValueOverride> },
    control: FormArray | FormGroup | FormControl
  ) {
    if (options.disabled) {
      options.disabled.subscribe(state => ControlBinder.disabledCallback(state, control));
    }
    if (options.valueOverride) {
      options.valueOverride.subscribe(state => ControlBinder.valueOverrideCallback(state, control));
    }
  }

  private static disabledCallback(formDisable: FormDisable, control: FormArray | FormGroup | FormControl): void {
    const isBoolean = typeof formDisable === 'boolean';
    const state = isBoolean ? formDisable : (formDisable as FormDisableOptions).enabled;
    if (control.disabled !== state) {
      (state ? control.disable : control.enable).bind(control)(isBoolean ? undefined : formDisable as FormDisableOptions);
    }
  }

  private static valueOverrideCallback(override: ValueOverride, control: FormArray | FormGroup | FormControl): void {
    control.setValue(override.value, override.options);
  }

}
